.content-wrap .txt-box {
  position: absolute;
  bottom: 0;
  left: 20px;
  height: 56px;
  width: 178px;
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
}

.content-wrap .txt-box .txt-info {
  width: 170px;
  padding-bottom: 3px;
  font: 700 22px/26px "comic", sans-serif;
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  /* 라인수 */
}

