		//content
		#container{}
		#content{}
		.content-wrap{
			&__outer{}
			&__inner{}
			.swiper{padding: 160px 0 0;height: 768px;position: relative;box-sizing:border-box;
				&-container{height: 493px;}
				&-wrapper{}
				&-controller{
					.btn-swiper-prev{position: absolute;top: 325px;left: 15px;width: 44px;height: 72px;background: url("/assets/images/club_house_reader/btn_prev01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;z-index: 3;
						&.swiper-button-disabled{opacity: 0;}
					}
					.btn-swiper-next{position: absolute;top: 325px;right: 15px;width: 44px;height: 72px;background: url("/assets/images/club_house_reader/btn_next01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;z-index: 3;
						&.swiper-button-disabled{opacity: 0;}
					}
				}
				&-slide{padding-top: 22px;z-index: 1;}
			}
			.clubhouse-reader{
				&__area{}
				&__box{}
				&__bg{}
				&__items{width: 826px;height: 471px;margin: 0 auto;display: flex;align-items: center;justify-content: center;flex-wrap: wrap;
					&.length1{
						.clubhouse-reader__item{
							&:nth-of-type(1){margin: 0 56px;}
							&:nth-of-type(2){display: none;}
							&:nth-of-type(3){display: none;}
							&:nth-of-type(4){display: none;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length2{
						.clubhouse-reader__item{
							&:nth-of-type(1){margin: 0 56px;}
							&:nth-of-type(2){margin: 0 56px;}
							&:nth-of-type(3){display: none;}
							&:nth-of-type(4){display: none;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length3{
						.clubhouse-reader__item{
							&:nth-of-type(1){margin-right: 68px;}
							&:nth-of-type(2){margin-right: 68px;}
							&:nth-of-type(3){margin-right: 40px;}
							&:nth-of-type(4){display: none;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length4{
						.clubhouse-reader__item{
							&:nth-of-type(1){margin: 0 56px;}
							&:nth-of-type(2){margin: 0 56px;}
							&:nth-of-type(3){margin: 27px 56px 0;}
							&:nth-of-type(4){margin: 27px 56px 0;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length5{
						.clubhouse-reader__item{
							&:nth-of-type(1){margin-right: 68px;}
							&:nth-of-type(2){margin-right: 68px;}
							&:nth-of-type(3){margin-right: 40px;}
							&:nth-of-type(4){margin: 27px 87px 0 0;}
							&:nth-of-type(5){margin: 27px 0 0;}
						}
					}
				}
				&__item{position: relative;width: 215px;height: 222px;display: block;
					@for $i from 1 through 5{
						&.bg-color0#{$i}{background: url("/assets/images/club_house_reader/bg_item0#{$i}.png") no-repeat 0 0;}
					}
					&.default{
					}
					&.disabled{
						.tit-week,
						.txt-info{opacity: 0.4;}
					}
					&.complete{
						&:before{content:"";position: absolute;top: 0;right: -24px;width: 49px;height: 49px;background: url("/assets/images/club_house_reader/icon_complete01.png") no-repeat 0 0;}
					}
					&.vacation{background: url("/assets/images/club_house_reader/bg_item_vacation.png") no-repeat 0 0;
						.tit-week{color: #d4dde1;}
						.img{display: none;}
					}
					&.this-week{
						&:before{content:"";position: absolute;top: -30px;right: -55px;width: 100px;height: 260px;animation: this-week-icon 1s steps(2) infinite forwards;background: url("/assets/images/sprite/club_house_reader_this_week.png") no-repeat 0 0;}
						.tit-week,
						.txt-info{color: #fff509;}
						.icon{
							&-txt-week{width: 77px;height: 24px;background: url("/assets/images/club_house_reader/txt_week01_active.png") no-repeat 0 0;}
							&-num{
								@for $i from 1 through 30 {
									&#{$i}{display: inline-block;@include sprite(nth($club-house-num-active-sprites, $i));}
								}
							}
						}
					}
					.tit-week{position: absolute;top: 0;left: 0;width: 100%;padding-top: 12px;height: 30px;display: flex;align-content: center;justify-content: center;@include font($nsroundecp, $font-b, 30px, 30px, #fff);letter-spacing: 1px;
						.number-box{margin: 3px 0 0 10px;}
					}
					.img{position: absolute;top: 41px;left: 18px;overflow: hidden;width: 178px;height: 126px;border-radius: 14px;}
					.icon{
						&-txt-week{width: 77px;height: 24px;background: url("/assets/images/club_house_reader/txt_week01.png") no-repeat 0 0;}
						&-num{
							@for $i from 1 through 30 {
								&#{$i}{display: inline-block;@include sprite(nth($club-house-num-sprites, $i));}
							}
						}
					}
				}
			}
			.txt-box{position: absolute;bottom: 0;left: 20px;height: 56px;width: 178px;display: flex;align-content: center;justify-content: center;flex-direction: column;overflow: hidden;
				.txt-info{width: 170px;padding-bottom: 3px;@include font($comic, $font-b, 22px, 26px, #fff);@include multi-text-overflow(2);}
			}
		}
	//}
